<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hideAddSongList">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box placeholder="搜索歌曲" @query="onQuery" ref="searchBox"></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
        <div class="list-wrapper">
          <scroll v-if="currentIndex === 0" :data="playHistory" class="list-scroll" ref="scroll" :refreshDelay="refreshDelay">
            <div class="list-inner">
              <singer-list :songs="playHistory" @select="selectSong"></singer-list>
            </div>
          </scroll>
          <scroll :refreshDelay="refreshDelay" ref="searchList" v-if="currentIndex===1" class="list-scroll"
                  :data="searchHistory">
            <div class="list-inner">
              <search-list :searches="searchHistory" @select="addquery" @delete="deleteSearchHistory"></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query" ref="searchResult">
        <suggest :query="query" @inputBlur="blurIn" @select="selectSuggest" ref="suggest"></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="top-title">
          <i class="icon-ok"></i>
          <span class="text">已添加歌曲到播放列表</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'
  import Suggest from 'components/suggest/suggest'
  import {searchMixin} from 'common/js/mixin'
  import Switches from 'base/switches/switches'
  import {mapGetters, mapActions} from 'vuex'
  import Scroll from 'base/scroll/scroll'
  import SingerList from 'base/singer-list/singer-list'
  import Song from 'common/js/song'
  import TopTip from 'base/top-tip/top-tip'
  import SearchList from 'components/search-list/search-list'

  export default {
    mixins: [searchMixin],
    data () {
      return {
        showFlag: false,
        showSinger: false,
        switches: [
          {name: '播放历史'},
          {name: '搜索历史'}
        ],
        currentIndex: 0
      }
    },
    methods: {
      show () {
        this.showFlag = true
      },
      hide () {
        this.showFlag = false
      },
      hideAddSongList () {
        this.showFlag = false
      },
      switchItem (index) {
        this.currentIndex = index
      },
      selectSong (song, index) {
        this.insertSong(new Song(song))
        this.$refs.topTip.show()
      },
      selectSuggest () {
        this.$refs.topTip.show()
        this.searchlist()
      },
      ...mapActions([
        'insertSong',
        'deleteSearchHistory'
      ])
    },
    computed: {
      ...mapGetters([
        'playHistory'
      ])
    },
    components: {
      SearchBox,
      Suggest,
      Switches,
      Scroll,
      SingerList,
      TopTip,
      SearchList
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
      .close
        position: absolute
        top: 0
        right: 8px
        .icon-close
          display: block
          padding: 12px
          font-size: 20px
          color: $color-theme

    .search-box-wrapper
      margin: 20px
    .shortcut
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 0
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>



